<script>
	export default{
		data(){	
			const date = new Date()
			const years = []
			const nowYear = date.getFullYear()
			const months = []
			const nowMonth = date.getMonth() + 1
			const days = []
			const nowDay = date.getDate()
			for (let i = 1970; i <= date.getFullYear(); i++) {
				years.push(i)
			}
			for (let i = 1; i <= 12; i++) {
				months.push(i)
			}
			for (let i = 1; i <= 31; i++) {
				days.push(i)
			}
			return{
				inputData:'',  //输入框数据
				gender:0,
				src1:'',
				srcId:-1,
				leftt:'../../static/mlestones/left2.png',
				leftL:'../../static/mlestones/left3.png',
				leftR:'../../static/mlestones/left1.png',
				rightT:'../../static/mlestones/right5.png',
				rightL:'../../static/mlestones/right6.png',
				rightR:'../../static/mlestones/right4.png',
				srcN:'../../static/mlestones/Female.png',
				srcNv:'../../static/mlestones/Female.png',
				srcNv_:'../../static/mlestones/nv.png',
				srcM:'../../static/mlestones/man.png',
				srcMan:'../../static/mlestones/Male.png',
				srcMan_:'../../static/mlestones/man.png',
				csmon:[],  //选中的日期
				t_day:'',
				value: [1, 1, 1], 
				val:[1, 1, 1], //索引值
				daysInMonth:[31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
				years,
				nowYear,
				months, 
				nowMonth,
				days,
				nowDay,
			}
		},
		methods:{
			// 获取当前月份天数
			getDays(year,month){
				if(month == 2){
					return ((year % 4 === 0) && ((year % 100) !== 0)) || (year % 400 === 0) ? 29 : 28
				}
				else{
					return this.daysInMonth[month - 1]
				}
			},
			getDate(){
				var _this = this
				const date = new Date()
				const years = []
				const year = date.getFullYear()
				const months = []
				const month = date.getMonth() + 1
				let daysInMonth = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
				const days = []
				const day = date.getDate()
				const das = new Date(2021,day+1,0)
				for (let i = 1980; i <= date.getFullYear(); i++) {
						years.push(i)
				}
				for (let i = 1; i <= 12; i++) {
						months.push(i)
				}
				for (let i = 1; i <= daysInMonth[month-1]; i++) {
						days.push(i)
				}
				console.log(daysInMonth[month-1]);
			},
			imgLeftTClick(){
				this.src1= this.leftt
				this.srcId = 1
			},
			imgLeftLClick(){
				this.src1= this.leftL
				this.srcId = 2
			},
			imgLeftRClick(){
				this.src1= this.leftR
				this.srcId = 0
			},
			imgRightTClick(){
				this.src1= this.rightT
				this.srcId = 4
			},
			imgRightRClick(){
				this.src1= this.rightR
				this.srcId = 3
			},
			imgRightLClick(){
				this.src1= this.rightL
				this.srcId = 5
			},
			switchGenderMan(){
				this.gender = 0
				this.srcM = this.srcMan_
				this.srcN = this.srcNv
			},
			switchGenderNv(){ 
				this.gender = 1
				this.srcN = this.srcNv_
				this.srcM = this.srcMan
			},
			async getInputDate(){
				var _this = this
				if(this.srcId == -1){
					this.$refs.Add.openPopup('Choose your avatar!')
					return false
				}
				if(this.inputData == ''){
					this.$refs.Add.openPopup('Please enter your name!')
					return false
				}
				// console.log(this.csmon.length);
				//把获取的日期处理
				if(_this.csmon.length === 0){
					// console.log('空');
					_this.t_day = "1971-02-02"
					console.log(_this.t_day);
				}else{
					// console.log('不为空');
					if(_this.csmon[1]<10){
						_this.csmon[1] = '0' + _this.csmon[1] 
					}
					if(_this.csmon[2]<10){
						_this.csmon[2] = '0' + _this.csmon[2] 
					}
					_this.t_day = _this.csmon[0] + '-' + _this.csmon[1] + '-' + _this.csmon[2]
					console.log('发送的数据');
					console.log(_this.t_day);
					console.log(_this.inputData);
					console.log(_this.gender);
					console.log(_this.srcId);
					
				}
				const res = await this.$myRequest({
					url:'/api/player/create',
					method:"POST",
					data:{
						"nickname":_this.inputData,
						// "birthDate":_this.t_day,
						// "gender":_this.gender,
						"avatarId":_this.srcId
					}
				})
				console.log(res);
				if(res.data.succeeded){
					_this.inputData = ''
					_this.src1 = ''
					_this.$emit("mOpenPopup") 
					// console.log("到这来");
					_this.$emit("cutBack") 
					_this.$emit('getMyData')
					// _this.$emit("mOpenPopup")
				}else{
					this.$refs.Add.openPopup('fail to add')
				}
			},
			
			// 滚动结束
			bindChange(e){ 
				this.val = e.detail.value
				let y = this.years[e.detail.value[2]]
				let mon = this.months[e.detail.value[1]]
				let day_s = this.months[e.detail.value[0]]
				let sss = this.getDays(y,mon) 
				
				console.log(day_s);
				this.csmon = [y,mon,day_s] //选中的年月日
				console.log(this.csmon);
				let newArr = [] 
				for (let i = 1; i <= sss; i++) {
					if(newArr.indexOf(this.days[i]) === -1){
						newArr.push(i);
					}
				}
				if(day_s == 1){
					this.days = this.days
				} 
				if(day_s != 1 || y !=1970){
					this.days = newArr
				}
				this.days = newArr  //把获取的新日期赋值给数组 
				// console.log(this.days);
			},
			
		},
	}
</script> 

